import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import{HttpModule} from '@angular/http';
import{FormsModule} from'@angular/forms';



import { AppComponent } from './app.component';
import { ViewListComponentComponent } from './view-list-component/view-list-component.component';
import { AddListComponentComponent } from './add-list-component/add-list-component.component';
import { ListService } from './service/list-service.service';


// Import the Animations module
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// Import the ButtonsModule
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { GridModule } from '@progress/kendo-angular-grid';

@NgModule({
  declarations: [
    AppComponent,
    ViewListComponentComponent,
    AddListComponentComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    BrowserAnimationsModule,
    ButtonsModule,
    GridModule
   
    
  ],
  providers: [ListService],
  bootstrap: [AppComponent]
})
export class AppModule { }



