import { Component, OnInit,Output, EventEmitter,Input  } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import{ListService} from '../service/list-service.service';
import{List} from'../model/list'


@Component({
  selector: 'app-add-list-component',
  templateUrl: './add-list-component.component.html',
  styleUrls: ['./add-list-component.component.css']
})

export class AddListComponentComponent implements OnInit {
@Input() list: List;
@Output() addList:EventEmitter<List>=new EventEmitter<List>();
private newList:List;

  constructor(private listServ:ListService) { }

  ngOnInit() {
    this.newList={
      title:'',
      category:'',
      description:'',
      _id:''
    }
  }
  public reset() {
    // this.newList.reset({
    //   _id: this.list._id,
    //   title:this.list.title,
    //   description:this.list.description,
    //   category:this.list.category
    // });
    this.newList={
      title:'',
      category:'',
      description:'',
      _id:''
    }  
  }

  public onSubmit() {
  	console.log(this.newList.category);
  	this.listServ.addList(this.newList).subscribe(
  		response=> {		
  			if(response.success== true)
          this.addList.emit(this.newList);
          this.reset();
  		},
	);

	}

}



